}
.shadow-name:hover .shadow-name__title {
  color: #fff;
}
.fadeIn {
  transition: 1s ease-out opacity, 1s ease-out transform;
  opacity: 0;
  transform: translateY(50px);
}
@media only screen and (max-width: 425px) {
  .fadeIn {
    opacity: 1;
    transform: translateY(0px);
  }
}
.fadeIn--delay05 {
  transition: 1s 0.5s ease-out opacity, 1s 0.5s ease-out transform;
}
.fadeIn--delay1 {
  transition: 1s 1s ease-out opacity, 1s 0.5s ease-out transform;
}
.fadeIn--delay15 {
  transition: 1s 1.5s ease-out opacity, 1s 0.5s ease-out transform;
}
.fadeIn--water {
  transform: translate(-40px, 40px);
}
.fadeIn--show {
  opacity: 1;
  transform: translateY(0px);
}
.fadeIn--show.fadeIn--water {
  transform: translate(0px, 0px);
}
